import { FC } from 'react'
import {
  Container,
  ExitIcon,
  MenuLink,
  PolygonImg,
  PolygonImgContainer,
  ReportsDropDown,
} from './styled'

interface IProps {
  open: boolean
  item: string
  onCloseReports: (value: number | false) => void
}
const MenuHeader: FC<IProps> = ({ open, item, onCloseReports }) => {
  const forecastMenu = [
    {
      name: '  ',
      link: 'forecast_statistics',
    },
    {
      name: '',
      link: 'forecast',
    },
  ]

  const referencBooks = [
    {
      name: '  ',
      link: 'adress',
    },
    {
      name: '',
      link: 'forecast_statistics',
    },
    {
      name: 'SKU',
      link: 'sku',
    },
    {
      name: '',
      link: 'prices',
    },
    {
      name: ' ',
      link: 'data_items',
    },
    {
      name: '',
      link: 'stocks',
    },
  ]

  const logIn = [
    {
      name: ' ',
      link: 'profile',
    },
    {
      name: ' ',
      link: 'userInfo',
    },
    {
      name: '',
      link: 'logOut',
    },
  ]

  return (
    <Container open={open} item={item}>
      <PolygonImgContainer item={item}>
        <PolygonImg />
      </PolygonImgContainer>

      <ReportsDropDown>
        {item === '' &&
          forecastMenu.map((item) => (
            <MenuLink
              key={item.name}
              to={item.link}
              onClick={() => onCloseReports(false)}
            >
              {item.name}
            </MenuLink>
          ))}
        {item === '' && (
          <MenuLink to="soon" onClick={() => onCloseReports(false)}>
              ...
          </MenuLink>
        )}
        {item === '' &&
          referencBooks.map((item) => (
            <MenuLink
              key={item.name}
              to={item.link}
              onClick={() => onCloseReports(false)}
            >
              {item.name}
            </MenuLink>
          ))}
        {item === 'LogIn' &&
          logIn.map((item) =>
            item.name === '' ? (
              <MenuLink
                key={item.name}
                item={item.name}
                to={item.link}
                onClick={() => onCloseReports(false)}
              >
                <ExitIcon />
                {item.name}
              </MenuLink>
            ) : (
              <MenuLink
                key={item.name}
                to={item.link}
                onClick={() => onCloseReports(false)}
              >
                {item.name}
              </MenuLink>
            )
          )}
      </ReportsDropDown>
    </Container>
  )
}

export default MenuHeader
